:root {
  --color-primary: #5ec576;
  --color-secondary: #ffcb03;
  --color-tertiary: #ff585f;
  --color-primary-darker: #4bbb7d;
  --color-secondary-darker: #fb0;
  --color-tertiary-darker: #fd424b;
  --color-primary-opacity: #5ec5763a;
  --color-secondary-opacity: #ffcd0331;
  --color-tertiary-opacity: #ff58602d;
  --gradient-primary: linear-gradient(to top left, #39b385, #9be15d);
  --gradient-secondary: linear-gradient(to top left, #ffb003, #ffcb03);
}

body {
  text-align: center;
  background-color: #bfeaf5;
  margin: 0;
  font-family: Merriweather, serif;
}

.top-div {
  background-color: #bfeaf5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

hr {
  border: 10px #678983;
  border-top-style: dotted;
  border-bottom: none;
  width: 15%;
  margin: 20px auto 40px;
}

h1 {
  color: #ffb100;
  margin: 50px 0 0;
  font-family: Sacramento, cursive;
  font-size: 5.625rem;
  line-height: 1.5;
}

h2 {
  color: #239636;
  margin: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 2.5rem;
  font-weight: normal;
}

h3 {
  color: #62b6b7;
  font-family: Montserrat, sans-serif;
}

.middle-div {
  background-color: #bfeaf5;
  padding: 0 10vw;
  font-size: larger;
}

.bottom-div {
  word-spacing: 2rem;
  background-color: #62b6b7;
  margin-top: 60px;
  padding: 2.5rem;
  line-height: 2.8;
}

.front {
  z-index: 1;
  padding-bottom: 50vh;
}

#mountain {
  z-index: 0;
  width: 70vw;
  max-width: 1000px;
  height: 75vh;
  max-height: 800px;
  position: absolute;
  bottom: 2%;
}

#bottom-cloud {
  position: absolute;
  left: 100px;
}

#top-cloud {
  position: relative;
  left: 200px;
}

.skills, .projects {
  height: 40px;
  position: relative;
  top: 0;
  bottom: 20px;
}

.skill-row {
  text-align: left;
  width: 65%;
  margin: 100px auto;
  line-height: 1.6;
}

.btn {
  background: #3498db -moz-linear-gradient(top, #11cdd4, #11999e);
  background-image: -ms-linear-gradient(top, #11cdd4, #11999e);
  -webkit-border-radius: 28px;
  -moz-border-radius: 28px;
  color: #fff;
  background-image: linear-gradient(#11cdd4, #11999e);
  border-radius: 8px;
  padding: 10px 20px;
  font-family: Arial;
  font-size: 20px;
  text-decoration: none;
}

.btn:hover {
  background: #3cb0fd -moz-linear-gradient(top, #30e3cb, #2bc4ad);
  background-image: -ms-linear-gradient(top, #30e3cb, #2bc4ad);
  background-image: linear-gradient(#30e3cb, #2bc4ad);
  text-decoration: none;
}

.time-description {
  padding-bottom: 20px;
}

.contact-me {
  padding: 0;
}

.cs {
  float: left;
  height: 8rem;
  padding: 0 30px 0 0;
}

.engineer {
  float: right;
  height: 8rem;
  padding: 0 0 0 30px;
}

.coffee {
  float: left;
  height: 8rem;
  padding: 0 30px 0 0;
}

.profile {
  padding: 50px 0;
}

.footer-link {
  color: #11999e;
  text-decoration: none;
}

.footer-link:hover {
  color: #ff0;
  text-decoration: none;
}

.copy-right {
  word-spacing: normal;
  color: #eaf6f6;
  margin: 0;
}

.lan-list {
  flex-direction: column;
  display: flex;
}

.middel-anchor {
  display: contents;
}

.slider {
  max-width: 100rem;
  height: 35rem;
  margin: 7rem auto;
  position: relative;
  overflow: hidden;
}

.slide {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 35rem;
  transition: transform 1s;
  display: flex;
  position: absolute;
  top: 0;
}

.slide > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.slider__btn {
  z-index: 10;
  color: #333;
  cursor: pointer;
  background: #ffffffb3;
  border: none;
  border-radius: 50%;
  width: 5.5rem;
  height: 5.5rem;
  font-family: inherit;
  font-size: 3.25rem;
  position: absolute;
  top: 50%;
}

.slider__btn--left {
  left: 6%;
  transform: translate(-50%, -50%);
}

.slider__btn--right {
  right: 6%;
  transform: translate(50%, -50%);
}

.dots {
  display: flex;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
}

.dots__dot {
  opacity: .7;
  cursor: pointer;
  background-color: #b9b9b9;
  border: none;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  margin-right: 1.75rem;
  transition: all .5s;
}

.dots__dot:last-child {
  margin: 0;
}

.dots__dot--active {
  opacity: 1;
  background-color: #888;
}

.testimonial {
  width: 65%;
  position: relative;
}

.testimonial__header {
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1.5rem;
  font-size: 1.75rem;
  font-weight: 500;
  display: flex;
}

.testimonial__text {
  color: #666;
  font-size: 1.2rem;
}

.testimonial__photo {
  border-radius: 30%;
  grid-row: 1 / span 2;
  width: 20vw;
}

